.movie {
  height: calc(100vh - 64px);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  &__dark {
    position: absolute;
    top: 64;
    left: 0;
    width: 100%;
    height: calc(100% - 64px);
    background-color: black;
    opacity: 0.7;
  }

  > .ant-row {
    height: 100%;
    display: flex;
    align-items: center;
  }

  &__poster {
    height: 70%;
    div {
      height: 100%;
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
    }
  }

  &__info {
    height: 70%;
    color: white;
    &-header {
      display: flex;
      align-items: center;
      justify-content: space-between;

      h1 {
        color: white;
        margin: 0;
        font-size: 60px;

        span {
          font-size: 20px;
          margin-left: 10px;
          opacity: 0.5;
        }
      }
      button {
        height: 100%;
        background-color: transparent;
        color: white;
        border: 0;
        font-size: 25px;

        &:hover,
        &:focus,
        &:active {
          cursor: pointer;
          background-color: transparent;
          color: turquoise;
        }
      }
    }
    &-content {
      margin-top: 50px;
      h3 {
        color: white;
        font-size: 28px;
        font-weight: bold;
      }
      p {
        font-size: 18px;
      }
    }
  }
}
